

/*before*/
.todoListMain .header input {
  padding: 10px;
  font-size: 16px;
  border: 2px solid rgba(253, 92, 99, 0.4);
  width: 165px;
  border-radius: 10px;
}
.todoListMain .header input:focus {
  border: 2px solid rgba(253, 92, 99, 0.8);
  outline: none;
}
.todoListMain .header button {
  padding: 10px;
  font-size: 16px;
  margin: 10px;
  margin-right: 0px;
  background-color: rgba(253, 92, 99, 1);
  color: #FFF;
  border: 2px solid rgba(253, 92, 99, 1);
  border-radius: 10px;
  outline: none;
}
.todoListMain .header button:hover {
  background-color: rgba(253, 92, 99, 0.4);
  border: 2px solid rgba(253, 92, 99, 0.4);
  cursor: pointer;
}
.todoListMain .theList {
  list-style: none;
  padding-left: 0;
  width: 250px;
}
.todoListMain .theList li {
  color: #333;
  background-color: rgba(255,255,255,.5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  border: 1px solid rgba(253, 92, 99, 0.4);
  -webkit-transition: background-color .2s ease-out;
  -o-transition: background-color .2s ease-out;
  transition: background-color .2s ease-out;
}

.todoListMain .theList li:hover {
  background-color: rgba(253, 92, 99, 0.2);
  cursor: pointer;
}



